.bg-overlay {
  background: -webkit-radial-gradient(
      rgba(119, 119, 119, 0.5),
      rgba(0, 100, 0, 0.7),
      rgba(0, 0, 100, 0.9)
    ),
    url(/memory-game/static/media/mem-home.3121dacc.png);
  background: radial-gradient(
      rgba(119, 119, 119, 0.5),
      rgba(0, 100, 0, 0.7),
      rgba(0, 0, 100, 0.9)
    ),
    url(/memory-game/static/media/mem-home.3121dacc.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  color: #fff;
  height: 100vh;
  padding-top: 50px;
}

.ulist {
  font-size: 2rem;
  padding-top: 1rem;
}

header {
  position: relative;
  background-color: black;
  height: 75vh;
  min-height: 25rem;
  width: 100%;
  overflow: hidden;
}

header video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

header .container {
  position: relative;
  z-index: 2;
}

header .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: 0.5;
  z-index: 1;
}
.Home .ulist li {
  list-style: none;
}

@media (pointer: coarse) and (hover: none) {
  header {
    background: url("https://source.unsplash.com/XT5OInaElMw/1600x900") black
      no-repeat center center scroll;
  }
  header video {
    display: none;
  }
}



