.backgroundImg{
  width: 100%;
  -webkit-transition: 0.7s ease-out;
  transition: 0.7s ease-out;
}
.initialCardsWrapper{
  overflow: hidden;
}

.card, .card_active, .initialCardsWrapper{
  position: relative;
  display: inline-block;
  -webkit-transition: 0.7s ease-out;
  transition: 0.7s ease-out;
  width: 120px;
  height: 120px;
}

.card, .card_active{
  cursor: pointer;
  margin: 0 3px;
  border-radius: 20px;
}
.initialCardsWrapper{
  border-radius: 10px;
  border: 2px solid;
  cursor:default;
}

.initialCards{
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid;
}

.cardFront, .cardBack, .cardBack_matched {
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 20px;
  border: 2px solid;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.cardFront, .cardBack, .cardBack_matched  {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: -webkit-transform 0.6s;
  transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  transition: transform 0.6s, -webkit-transform 0.6s;
}

.cardBack{
  background-color: white;
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}


.initialCardsWrapper:hover{
  transform: rotateY( 20deg );
  -ms-transform: rotateY( 20deg );
  -webkit-transform: rotateY( 20deg );
  -webkit-transition: 0.8s ease-out;
  transition: 0.8s ease-out;
}
.card:hover .cardFront, .cardBack, .cardBack_matched{
  border: 2px solid white;
  z-index: 1;
  -webkit-transition: 0.4s ease-out;
  transition: 0.4s ease-out;
}

.cardBack_matched{
  background-color: #239a63;
  border: 2px solid gold;
  -webkit-animation: cardMatched 2s 1;
          animation: cardMatched 2s 1;
}

@-webkit-keyframes cardMatched {
  from{
    border: 2px solid gold;
    background-color: gold;
  }
  to{
    border: 2px solid gold;
    background-color: #239a63;
  }
}

@keyframes cardMatched {
  from{
    border: 2px solid gold;
    background-color: gold;
  }
  to{
    border: 2px solid gold;
    background-color: #239a63;
  }
}

.card:hover .gameNameOn{
  color: gold;
}

.card_active .cardFront {
  -webkit-transform: rotateY(-180deg);
          transform: rotateY(-180deg);
}

.card_active .cardBack  {
  -webkit-transform: rotateY(0);
          transform: rotateY(0);
}

.gameName, .gameNameOn{
  vertical-align: middle;
  color: rgba(255, 255, 255, 0.8);
  -webkit-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
}
.gameName, .gameNameOn{
  line-height: 120px;
  font-weight: bold;
}

.gameNameOn{
  line-height: 124px;
  font-size: 50px;
}

@media only screen and (max-device-width: 480px) {

  .card, .card_active, .initialCardsWrapper {
    width: 80px;
    height: 80px;
  }

  .gameName{
    font-size: 0.6em;
    line-height: 80px;
  }
  .gameNameOn{
    font-size: 2.5em;
    line-height: 80px;
  }


}

.gameBoard,
.gameBoard_complete {
  height: 80vh;
  text-align: center;
  text-align: -webkit-center;
  text-align: -moz-center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  overflow: auto;
  position: relative;
}

.cardsWrapper {
  margin: auto;
  max-width: 550px;
}

.gameBoardBack {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white;
  -webkit-transform: rotateY(-180deg);
  transform: rotateY(-180deg);
}

.cardsWrapper,
.gameBoardBack {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: -webkit-transform 0.6s;
  -webkit-transition: -webkit-transform 3s;
  transition: -webkit-transform 3s;
  transition: transform 3s;
  transition: transform 3s, -webkit-transform 3s;
}

.gameBoard_complete .cardsWrapper {
  -webkit-transform: rotateY(-720deg);
  transform: rotateY(-720deg);
}

@media only screen and (max-device-width: 480px) {
  .gameBoard {
    height: 80vh;
  }
}

.scoreBoard {
  margin: 0 auto;
  width: 100%;
  height: 60px;
  position: absolute;
  top: 50%;
  z-index: 999;
}

.scoreBoard--active {
  background: rgba(255, 50, 50, 0.3);
  border-radius: 10px;
  margin: 0 auto;

  width: 80%;
  min-height: 60px;
  position: relative;
  -webkit-transition: 1s ease-out;
  transition: 1s ease-out;
}

.gameButton {
  padding: 0 10px;
  font-size: 15px;
  height: 40px;
  -webkit-transition: 0.8s ease-out;
  transition: 0.8s ease-out;
  background-color: white;
  -webkit-animation: titleButtonLight 0.9s infinite;
          animation: titleButtonLight 0.9s infinite;
  cursor: pointer;
  -webkit-animation: smokeEffect 0.9s forwards;
          animation: smokeEffect 0.9s forwards;
}

.gameButton:hover {
  background-color: gold;
  -webkit-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
  border-radius: 15px;
}

@-webkit-keyframes titleButtonLight {
  from {
    background-color: white;
  }
  to {
    background-color: gold;
  }
}

@keyframes titleButtonLight {
  from {
    background-color: white;
  }
  to {
    background-color: gold;
  }
}

.gameButton--active:hover {
  background-color: gold;
  -webkit-transition: 0.3s ease-out;
  transition: 0.3s ease-out;
}

.gameButton--active {
  position: absolute;
  top: 10px;
  right: 30px;
  height: 40px;
  -webkit-transition: 0.8s ease-out;
  transition: 0.8s ease-out;
  background-color: white;
  -webkit-animation: buttonIn 1.2s forwards;
          animation: buttonIn 1.2s forwards;
}

@-webkit-keyframes buttonIn {
  from {
    right: -230px;
  }
  to {
    right: 30px;
  }
}

@keyframes buttonIn {
  from {
    right: -230px;
  }
  to {
    right: 30px;
  }
}

.title {
  line-height: 60px;
  font-size: 30px;
  color: gold;
  -webkit-animation: smokeEffect 0.9s forwards;
          animation: smokeEffect 0.9s forwards;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 1);
}
.highestScore {
  top: 15px;
  left: 30px;
  font-size: 25px;
  position: absolute;
  color: orange;
  -webkit-animation: smokeEffect 0.9s forwards;
          animation: smokeEffect 0.9s forwards;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 1);
}

@-webkit-keyframes smokeEffect {
  from {
    transform: rotate(15deg) translateY(-200px);
    -webkit-transform: rotate(15deg) translateY(-200px);
    opacity: 0;
    -webkit-filter: blur(20px);
    filter: blur(20px);
  }
  to {
    transform: rotate(0deg) translateY(0px);
    -webkit-transform: rotate(0deg) translateY(0px);
    opacity: 1;
    -webkit-filter: blur(1px);
    filter: blur(0px);
  }
}

@keyframes smokeEffect {
  from {
    transform: rotate(15deg) translateY(-200px);
    -webkit-transform: rotate(15deg) translateY(-200px);
    opacity: 0;
    -webkit-filter: blur(20px);
    filter: blur(20px);
  }
  to {
    transform: rotate(0deg) translateY(0px);
    -webkit-transform: rotate(0deg) translateY(0px);
    opacity: 1;
    -webkit-filter: blur(1px);
    filter: blur(0px);
  }
}

@media only screen and (max-device-width: 480px) {
  .scoreBoard {
    top: 43%;
  }
  .scoreBoard--active {
    border-radius: 0px;
    width: 100%;
    margin-top: 0px;
  }
  .title {
    line-height: 60px;
    font-size: 20px;
    color: gold;
    -webkit-animation: smokeEffect 0.9s forwards;
            animation: smokeEffect 0.9s forwards;
    text-shadow: 2px 2px 2px rgba(0, 0, 0, 1);
  }
  .highestScore {
    top: 20px;
    left: 20px;
    font-size: 17px;
  }
  .gameButton--active {
    position: absolute;
    top: 15px;
    right: 30px;
    height: 30px;
    -webkit-transition: 0.8s ease-out;
    transition: 0.8s ease-out;
    background-color: white;
    -webkit-animation: buttonIn 1.2s forwards;
            animation: buttonIn 1.2s forwards;
  }
}

